body {
  background-color: #000;
  margin: 0;
  font-family: Arial, sans-serif;
  overflow: hidden;
}

@media (width <= 768px) {
  #player, .enemy, .item {
    width: 32px;
    height: 32px;
  }
}

#player, .enemy, .item {
  image-rendering: pixelated;
}

#game-container {
  background-color: #87ceeb;
  background-image: url("../sprites.b818d463.webp");
  background-position: 0 0;
  background-repeat: repeat;
  background-size: 1024px 1024px;
  width: 100vw;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

#player {
  z-index: 10;
  background-image: url("../sprites.b818d463.webp");
  background-position: -470px -370px;
  background-size: 1024px 1024px;
  width: 64px;
  height: 64px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.enemy {
  background-image: url("../sprites.b818d463.webp");
  background-position: -64px -128px;
  background-size: 1024px 1024px;
  width: 64px;
  height: 64px;
  position: absolute;
}

.item {
  background-image: url("../sprites.b818d463.webp");
  background-position: -128px -128px;
  background-size: 1024px 1024px;
  width: 32px;
  height: 32px;
  position: absolute;
}

#ui {
  color: #fff;
  z-index: 20;
  z-index: 15;
  background: #000000b3;
  border-radius: 5px;
  padding: 10px;
  font-family: "Press Start 2P", cursive;
  font-size: 14px;
  position: absolute;
  top: 20px;
  left: 20px;
}

.middle {
  gap: 10px;
  display: flex;
}

#joystick {
  z-index: 15;
  background: #00000080;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 120px;
  height: 120px;
  display: flex;
  position: absolute;
  bottom: 50px;
  left: 50px;
}

#joystick .control-stick {
  cursor: pointer;
  background: #fffc;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  transition: all .1s;
  position: absolute;
}

#joystick .control-stick:active {
  background: #c8c8c8;
}
